import React from "react"
import PropTypes from "prop-types"

export default class GwaHelpBtn extends React.Component {
  openWindow =() => {
    window.open("https://github.com/bcgov/gwa/wiki/Developer-Guide", "_blank")
  }

  render() {
    return (
      <div className="auth-wrapper">
        <button onClick={ this.openWindow } className={ "btn authorize" }>
          API Key Help
        </button>
      </div>
    )
  }
}



